import { useToast } from "vue-toastification";

class Toast {
  /**
   * Create a new Toast instance.
   */
  constructor() {
    this.toast = useToast();
    this.config = {
      position: "bottom-center",
      timeout: 5000,
      closeOnClick: true,
      pauseOnFocusLoss: true,
      pauseOnHover: true,
      showCloseButtonOnHover: false,
      closeButton: "button",
      icon: true,
    };
  }

  /**
   * Fire a toast.
   *
   * @param {string} message
   * @param {"success", "info", "error", "warning"} type
   * @param {object} config
   */
  fire(message, type = "info", config = {}) {
    this.toast[type](message, { ...this.config, ...config });
  }
}

export default new Toast();
